@media only screen and (max-width: 767px) { .about__wrap:before { display: none; } }

.about__head { position: relative; padding-right: 275px; }

@media only screen and (max-width: 767px) { .about__head { padding-right: 0; } }

.about__title { font-weight: 300; }

.about__best-price { position: absolute; top: -14px; right: 0; }

@media only screen and (max-width: 767px) { .about__best-price { display: none; } }

.about__list { margin-bottom: 0; }

.about__list li { position: relative; display: block; }

@media only screen and (max-width: 992px) { .about__list li { padding-left: 20px; } }

.about__list li:not(:first-child) { margin-top: 4px; }

.about__list li:before, .about__list li:after { position: absolute; content: ""; background-color: #d6d6d6; }

.about__list li:before { top: 7px; left: -17px; width: 1px; height: 7px; }

@media only screen and (max-width: 992px) { .about__list li:before { left: 3px; } }

.about__list li:after { top: 10px; left: -20px; width: 7px; height: 1px; }

@media only screen and (max-width: 992px) { .about__list li:after { left: 0; } }

.about__list a { color: #2e4281; font-size: 14px; font-weight: 400; border-bottom: 1px dotted; }

.best-price { color: #fff; display: table; }

.best-price__text { padding-right: 16px; max-width: 195px; color: #8d8d8d; font-size: 11px; font-weight: 600; text-align: right; vertical-align: middle; letter-spacing: -0.01em; display: table-cell; }

.best-price__btn { padding-top: 23px; width: 78px; height: 78px; text-align: center; font-size: 14px; font-weight: 700; line-height: 15px; border-radius: 50%; background-color: #c82f23; background-image: linear-gradient(to top, #be3d2b 0%, #ff2f06 100%); box-shadow: 2.5px 4.3px 21px rgba(0, 0, 0, 0.3); vertical-align: top; display: table-cell; }
